let pagina = 1;
const contenedorServicios = document.querySelector('.contenedor-servicios');

const cita = {
    nombreCliente: '',
    fecha: '',
    hora: '',
    servicios: []
}

document.addEventListener('DOMContentLoaded', () => {
    iniciarFunciones();
});

const iniciarFunciones = () => {
    mostrarServicios();
    mostrarSeccion();
    cambiarSeccion();
    paginaSiguiente();
    paginaAnterior();
    botonesPaginador();
    mostrarResumen();
    nombreCita();
    fechaCita();
    horaCita();
    DeshabilitarFechasAnteriores();
};

const mostrarServicios = () => {
    fetch('../../servicios.json')
    .then(res => res ? Promise.resolve(res) : Promise.reject(res))
    .then(res => res.json())
    .then(res => res.servicios.forEach(serv => {
        const {id, nombre, precio} = serv;

        const servicio = document.createElement('DIV');
        const titulo = document.createElement('H4');
        const costo = document.createElement('P');

        titulo.textContent = nombre;
        costo.textContent = ` $ ${precio}`;

        servicio.classList.add('servicios');
        servicio.dataset.idServicio = id;
        titulo.classList.add('titulo__servicios');
        costo.classList.add('precio__servicios');

        servicio.onclick = seleccionarServicio;



        servicio.appendChild(titulo);
        servicio.appendChild(costo);

        contenedorServicios.appendChild(servicio);


    }))
    .catch(res => console.log(res));
};

const seleccionarServicio = (e) => {
    let elemento;

    if(e.target.tagName === 'P' || e.target.tagName === 'H4') elemento = e.target.parentElement;
    else elemento = e.target;

    elemento.classList.toggle('seleccionado');

    if(elemento.classList.contains('seleccionado')){{
        const servicioObj = {
            id: parseInt(elemento.dataset.idServicio),
            nombre: elemento.firstElementChild.textContent,
            precio: elemento.lastElementChild.textContent
        }
        /* console.log(servicioObj) */
        agregarServicio(servicioObj);
    }}
    else{
        const id = parseInt(elemento.dataset.idServicio)

        eliminarServicio(id)
    }
};

const mostrarSeccion = () => {

    const seccionAnterior = document.querySelector('.mostrar-seccion')
    //SI EXISTE EL ELEMENTO CON LA CLASE MOSTRAR-SECCION, ENTONCES ELMINA LA CLASE
    if(seccionAnterior) seccionAnterior.classList.remove('mostrar-seccion');
    

    const paginaActual = document.querySelector(`#paso${pagina}`);
    paginaActual.classList.add('mostrar-seccion');

    const tabActivo = document.querySelector('.navegacion button.activo')
    //SI EXISTE EL ELEMENTO CON LA ACTIVO, ENTONCES ELMINA LA CLASE
    if(tabActivo) tabActivo.classList.remove('activo');

    const tab = document.querySelector(`[data-paso="${pagina}"]`);
    tab.classList.add('activo');
};

const cambiarSeccion  = () => {
    const enlaces = document.querySelectorAll('#nav button');
    
    enlaces.forEach(pag => {
        pag.addEventListener('click', (e) => {
            e.preventDefault();
            pagina = parseInt(e.target.dataset.paso);
            console.log(pagina)
            mostrarSeccion();


            const paginaAnterior = document.querySelector('#anterior');
            const paginaSiguiente = document.querySelector('#siguiente');

            if(pagina === 1){
                paginaAnterior.classList.add('ocultar');
            }
            else if(pagina === 3){
                paginaSiguiente.classList.add('ocultar');
                paginaAnterior.classList.remove('ocultar');
            }
            else{
                paginaAnterior.classList.remove('ocultar');
                paginaSiguiente.classList.remove('ocultar');
            }
        })
    })
};

const paginaSiguiente = () => {
    const paginaSiguiente = document.querySelector('#siguiente');
    paginaSiguiente.addEventListener('click', (e) => {
        pagina++;
        console.log(pagina)
        botonesPaginador();
    })
};

const paginaAnterior = () => {
    const paginaAnterior = document.querySelector('#anterior');
    paginaAnterior.addEventListener('click', () => {
        pagina--;
        botonesPaginador();
    })
};

const botonesPaginador = () => {
    const paginaAnterior = document.querySelector('#anterior');
    const paginaSiguiente = document.querySelector('#siguiente');

    if(pagina === 1){
        paginaAnterior.classList.add('ocultar');
    }
    else if(pagina === 2){
        paginaAnterior.classList.remove('ocultar');
        paginaSiguiente.classList.remove('ocultar');
    }
    else if(pagina === 3){
        paginaSiguiente.classList.add('ocultar');
        mostrarResumen()
    }
    

    mostrarSeccion();
};


const mostrarResumen = () => {
    const {nombreCliente, fecha, hora, servicios} = cita
    const contenedorTres = document.querySelector('#paso3')

    contenedorTres.innerHTML = ""

    if(Object.values(cita).includes('')){
        const serviciosIncompletos = document.createElement('P')
        serviciosIncompletos.textContent = 'Faltan datos por rellenar'
        serviciosIncompletos.classList.add('datos')
        contenedorTres.appendChild(serviciosIncompletos)
    }
    else{
        const heading = document.createElement('H2')
        const nombre = document.createElement('P')
        const fechaCita = document.createElement('P')
        const horaCita = document.createElement('P')
        const servicioCita = document.createElement('DIV')
        const headingServicios = document.createElement('H2')
        const totalPagar = document.createElement('P')

        heading.textContent = 'Resumen de Cita'
        nombre.innerHTML = `TÃº Nombre: <span> ${nombreCliente}</span>` 
        fechaCita.innerHTML = `La fecha solicitada es: <span>${fecha}</span>`
        horaCita.innerHTML = `La hora solicitada es: <span>${hora}</span>`
        headingServicios.textContent = `Resumen de tus Servicios`
        let cantidad = 0;

        servicios.forEach(e => {
            const contenedorServicio = document.createElement('DIV')
            contenedorServicio.classList.add('contenedor-servicio')
            const nombreServicio = document.createElement('P')
            const precioServicio = document.createElement('P')

            nombreServicio.textContent = e.nombre
            precioServicio.textContent = e.precio
            precioServicio.classList.add('precio-js')
            
            const totalServicio = e.precio.split('$')

            cantidad += parseInt(totalServicio[1].trim())

            contenedorServicio.appendChild(nombreServicio)
            contenedorServicio.appendChild(precioServicio)

            servicioCita.appendChild(contenedorServicio)
            totalPagar.innerHTML = `Total a Pagar: <span>${cantidad}</span>`
        })
        console.log(cantidad)
        nombre.classList.add('resumen__datos')
        fechaCita.classList.add('resumen__datos')
        horaCita.classList.add('resumen__datos')
        totalPagar.classList.add('pagar')
        

        contenedorTres.appendChild(heading)
        contenedorTres.appendChild(nombre)
        contenedorTres.appendChild(fechaCita)
        contenedorTres.appendChild(horaCita)
        contenedorTres.appendChild(headingServicios)
        contenedorTres.appendChild(servicioCita)
        contenedorTres.appendChild(totalPagar)
    }
};

const agregarServicio = (objeto) => {
    const { servicios } = cita;

    cita.servicios = [...servicios, objeto]

    console.log(cita)
};

const eliminarServicio = (id) => {
    const {servicios} = cita
    cita.servicios = servicios.filter(e => e.id !== id)

    console.log(cita)
};

const nombreCita = () => {
    const nombre = document.querySelector('#nombreCliente')
    nombre.addEventListener('input', (e) => {
       const valor = e.target.value.trim();

       if(valor === '' || valor.length < 2) mostrarAlerta('Nombre no valido', true);
       else{
           const alerta = document.querySelector('.nombre-error')
           if(alerta) alerta.remove()
        }
        cita.nombreCliente = valor
    });
};

const fechaCita = () => {
    const fechaInput = document.querySelector('#fecha')
    fechaInput.addEventListener('input', (e) => {
        const dia = new Date(e.target.value).getUTCDay();  
        if([0, 6].includes(dia)){
            e.preventDefault()
            fechaInput.value = ''
            mostrarAlerta('No se aceptan citas los fin de semana', true)
        }
        else {
            cita.fecha = fechaInput.value
        }
    });
};


const DeshabilitarFechasAnteriores = () => {
    const fechaInput = document.querySelector('#fecha')

    const fechaAhora = new Date();
    const year = fechaAhora.getFullYear();
    const mes = fechaAhora.getMonth() + 1;
    const dia = fechaAhora.getDate() + 1;
    //FORMATO QUE DESEO DD-MM-AAAA
    const fechaDeshabilitar = `${year}-${mes < 10 ? `0${mes}` : mes}-${dia}`
                                
    fechaInput.min = fechaDeshabilitar
}

const horaCita = () => {
    const horaInput = document.querySelector('#hora');
    horaInput.addEventListener('input', (e) => {
        const horaCita = e.target.value
        const hora = horaCita.split(':')

        if(hora[0] < 9 || hora[0] > 18) mostrarAlerta('Hora no valida rey', true)
        else cita.hora = horaCita
    })
}

const mostrarAlerta = (mensaje, error = null) => {
    const alertaPrevia = document.querySelector('.nombre-error')
    if(alertaPrevia) return;


    const formulario = document.querySelector('.formulario')
    const alerta = document.createElement('P');
    alerta.textContent = mensaje;
    if(error)alerta.classList.add('nombre-error')
    else alerta.remove()
    formulario.appendChild(alerta)

    setTimeout(() => {
        alerta.remove('nombre-error')   
    }, 3000);
};
